// SIDEBAR
.sidebar {
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  height: calc( 100vh - 70px);
  width: 360px;
  background-color: #09112a;
  transition: all 0.4s ease;
  // z-index: 100;
  @media only screen and (max-width: 1366px) {
    width: 320px;
  }
  .sidebar__row {
    display: flex;
    align-items: center;
    padding: 2rem 1rem 1.5rem;
    &-item {
      display: flex;
      align-items: center;
      &.is-active {
        color: #7783b5;
      }
      &:not(:last-child) {
        margin-right: 1.5rem;
      }
    }
  }
  .sidebar__online {
    font-size: 0.8rem;
    color: #fff;
    text-transform: uppercase;
    padding: 1rem 1rem;
    background-color: #0b1534;
    @media only screen and (max-height: 680px) {
      padding: 5px 0;
    }
    .online__label {
      margin-right: 5px;
      color: #8b9dac;
    }
    .icon {
      margin-right: 0.8rem;
      font-size: 1rem;
      fill: #5373c5;
    }
  }
  .chat {
    flex-grow: 1;
    height: 50vh;
  }
  .chat__feed {
    height: calc(100% - 50px);
    margin-right: 5px;
    .chat__item:nth-child(even) {
      background-color: #0b1534;
    }
  }
  .chat__item {
    display: flex;
    align-items: flex-start;
    padding: 2rem 10px 2rem;
    @media only screen and (max-width: 768px) {
      flex-direction: column;
    }
  }
  .chat__photo {
    display: flex;
    margin-right: 15px;
    margin-bottom: 5px;
    img {
      width: 40px;
      height: 40px;
      overflow: hidden;
    }
  }
  .chat__message {
    position: relative;
    font-size: 0.9rem;
    border-radius: 4px;
  }
  .message__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
    font-size: inherit;
    font-weight: 1100;
    color: white;
    time {
      font-size: 0.8rem;
      color: #596982;
    }
  }
  .message__text {
    margin-bottom: 0;
    letter-spacing: normal;
    color: #afa8b1;
  }
  // chat form
  .chat__form-wrap {
    position: relative;
    @media only screen and (max-height: 680px) {
      margin-top: 15px;
    }
  }
  .chat__input {
    width: 100%;
    height: 50px;
    padding-right: 63px;
    padding-left: 1rem;
    background-color: #0b1534;
    border: none;
    font-size: 0.9rem;
    color: #fff;
    &::placeholder {
      color: #b4c7d6;
      font-size: 0.7rem;
    }
  }
  .chat__buttons {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 5px;
    display: flex;
    align-items: center;
    margin: auto;
    button {
      padding: 5px 10px;
      font-size: 1rem;
      text-transform: uppercase;
      font-weight: 900;
      color: #777a8c;
      background-color: transparent;
      border: none;
    }
    .icon-smile {
      fill: #5373c5;
      cursor: pointer;
      &:hover {
        fill: lighten(#5373c5, 20%);
      }
    }
  }
  .chat__send {
    &:hover {
      cursor: pointer;
      color: #fff;
      .icon {
        fill: $dark-peach;
      }
    }
  }
  p {
    margin-bottom: 0;
  }
}

// left sidebar--toplist
.sidebar--toplist {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  color: #edf4ff;
  h4,
  h5,
  h6 {
    font-size: inherit;
    font-weight: 400;
  }
  .topheader {
    color: #fff;
    .btn--update {
      color: #7783b5;
      &:hover {
        .icon {
          fill: $dark-peach;
        }
      }
    }
  }
  .sidebar__hash {
    padding: 1rem;
    font-size: 0.9rem;
    background-color: #0b1534;
  }
  .sidebar__hash-inner {
    padding-left: 1.5rem;
  }
  .hash__title {
    position: relative;
    text-transform: uppercase;
    color: $dark-peach;
    &:after {
      content: "";
      position: absolute;
      left: -1.5rem;
      top: 0;
      bottom: 0;
      margin: auto;
      display: inline-block;
      width: 14px;
      height: 14px;
      background-color: #5373c5;
      border-radius: 50%;
    }
  }
  .hash__value {
    color: #8b9dac
  }
  .sidebar__winner {
    background-image: url('../img/bg-circles1.jpg');
    background-size: cover;
  }
  .vert {
    text-transform: uppercase;
    transform: rotate(-90deg) translateX(-1.8rem);
    transform-origin: center;
    margin-bottom: 0;
    font-weight: 1100;
    width: 12px;
    margin-right: 1rem;
    letter-spacing: 1px;
  }
  .winner__ava {
    max-width: 3.5rem;
    max-height: 3.5rem;
    margin-right: 1rem;
  }
  .winner__name {
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: 1.2rem;
  }
  .winner__won {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0.6rem 1.2rem;
    font-size: 0.9rem;
    background-image: url("../img/won.png");
    background-repeat: no-repeat;
    background-size: 100% 100%;
    transform: translateX(25%);
  }
  .sidebar__stores {
    padding: 1rem;
    justify-content: space-around;
    background-image: url("../img/bg-circles2.png");
    background-repeat: no-repeat;
    background-size: cover;
  }
  .sidebar__store {
    max-width: 100px;
  }
  .small {
    font-size: 0.7rem;
  }
}

.toplist {
  flex-grow: 1;
  height: 50vh;
}

.toplist__feed {
  height: 100%;
  .toplist__item:nth-child(even) {
    background: linear-gradient(to right, rgba(9, 17, 42, 1) 0%, rgba(11, 21, 52, 1) 95%, rgba(11, 21, 52, 1) 100%);
  }
}

.toplist__item {
  position: relative;
  display: flex;
  align-items: center;
  width: 100%;
  padding: 1rem;
  &:before,
  &:after {
    content: "";
    position: absolute;
    left: 6px;
    top: 0;
    bottom: 0;
    margin: auto;
  }
  &:after {
    display: inline-block;
    width: 5px;
    height: 5px;
    border-radius: 1.5px;
    background-color: #2af2ff;
  }
  &:before {
    left: 8px;
    border-left: 1.5px dotted #282d3e;
    opacity: 0.7;
  }
}

.toplist__item-inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  .small {
    color: #707793;
  }
}

.toplist__name {
  margin-bottom: 0.2rem;
  color: #edf4ff;
  font-size: 1rem;
}

.toplist__item-value {
  padding: 0.5rem;
  font-size: 0.8rem;
  border-radius: 2px;
  background-color: #172137;
}

.chat__photo {
  margin-bottom: 0;
}

@media only screen and (max-width: 1100px) {
  #sidebar {
    position: absolute;
    right: -320px;
    top: 70px;
    width: 320px;
    &.is-open {
      right: 0;
    }
  }
}


/*begin hamburger*/

#nav-icon1 {
  width: 24px;
  height: 24px;
  position: relative;
  transform: rotate(0deg);
  transition: 0.4s ease-in-out;
  cursor: pointer;
}

#nav-icon1 span {
  display: block;
  position: absolute;
  height: 2px;
  width: 100%;
  background: #777a8c;
  border-radius: 9px;
  opacity: 1;
  left: 0;
  transform: rotate(0deg);
  transition: 0.25s ease-in-out;
}

#nav-icon1 span:nth-child(1) {
  top: 0;
}

#nav-icon1 span:nth-child(2) {
  top: 8px;
}

#nav-icon1 span:nth-child(3) {
  top: 16px;
}

@media only screen and (max-height: 680px) {
  #nav-icon1 {
    width: 18px;
    height: 18px;
    span:nth-child(2) {
      top: 6px;
    }
    span:nth-child(3) {
      top: 12px;
    }
  }
}

#nav-icon1.open span:nth-child(1) {
  top: 5px;
  transform: rotate(135deg);
  @media only screen and (max-height: 680px) {
    top: 0;
  }
}

#nav-icon1.open span:nth-child(2) {
  opacity: 0;
  left: -60px;
}

#nav-icon1.open span:nth-child(3) {
  top: 5px;
  transform: rotate(-135deg);
  @media only screen and (max-height: 680px) {
    top: 0;
  }
}

@media only screen and (min-width: 1100px) {
  #touch-menu {
    display: none!important;
  }
}

@media only screen and (max-width: 1100px) {
  #touch-menu {
    position: absolute;
    right: 0;
    top: 70px;
    margin-top: 8px;
    margin-right: 10px;
    z-index: 101;
  }
}


/*end hamburger*/