@import "libs/select2";
@import "mixins";
@import "vars";
@import "generated/sprite-svg";
@import "sidebar";
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

:root {
  font-family: $font-primary;
  line-height: 1.6;
  * {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-overflow-scrolling: touch;
  }
}

html,
body {
  height: 100%;
  @include rfs(16);
  overflow: hidden;
}

.wrapper {
  width: 100%;
  color: #fefefe;
  font-family: $font-primary;
  background-color: #060d23;
  min-height: 100vh;
}

.icon {
  fill: #fff;
  transition: 0.3s all ease;
}

a {
  color: inherit;
  &:hover {
    color: inherit;
    text-decoration: none;
  }
}

.light-blue {
  color: #2af2ff
}

// Header 
.header {
  position: relative;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 70px;
  margin-bottom: 2px;
  &:after {
    content: "";
    position: absolute;
    left: 0;
    bottom: -2px;
    display: block;
    width: 100%;
    height: 2px;
    opacity: 0.06;
    background-color: #f3f6fa;
  }
}

.btn--balance {
  height: 70px;
  padding: 1vw 3vw;
  font-family: "GothamPro", $font-primary;
  @include rfs(14);
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.83;
  letter-spacing: 1.111px;
  text-align: left;
  color: #fefefe;
  border-radius: 0;
  background-image: url('../img/bg-circles.jpg');
  background-size: cover;
  .icon {
    margin-left: 0.5rem;
  }
}

// Main menu
.navbar {
  padding: .3rem 1rem;
}

.navbar-collapse {
  @media only screen and (max-width: 768px) {
    position: absolute;
    top: 59px;
    background-color: #1b2c49;
    z-index: 2;
  }
}

.main-menu {
  padding-left: 1vw;
  align-items: center;
  @media only screen and (min-width: 1500px) {
    padding-left: 10vw;
  }
  @media only screen and (max-width: 768px) {
    padding: 0.5em;
  }
  .nav-item {
    margin-right: 1vw;
    @media only screen and (max-width: 768px) {
      margin: 0;
    }
    &.active,
    .nav-link {
      border-color: #ffffff;
      &:hover {
        border-color: #ffa861;
      }
    }
    .dropdown-toggle::after {
      display: none;
    }
  }
  .nav-link {
    display: flex;
    align-items: center;
    transition: all 0.3s ease;
    cursor: pointer;
    font-family: "GothamPro", $font-primary;
    @include rfs(14);
    font-weight: bold;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.83;
    letter-spacing: 1.111px;
    text-align: left;
    color: #fefefe;
    @media only screen and (min-width: 768px) {
      padding: 12px 0;
    }
    &:hover {
      color: $dark-peach;
      border-color: $dark-peach;
      .icon {
        fill: $dark-peach;
      }
    }
    &.button {
      width: 155.2px;
      height: 50px;
      display: flex;
      justify-content: center;
      align-items: center;
      &:hover {
        color: #ffffff;
      }
    }
    .icon {
      margin-right: 1vw;
    }
  }
  .dropdown-menu {
    min-width: 10rem;
    padding: 0;
    margin: 0;
    font-size: 1em;
    border: none;
    border-radius: 0;
    box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.1), 0 8px 15px 0 rgba(0, 0, 0, 0.1);
  }
  .dropdown-item {
    color: #000000;
    padding: .25rem 0.6em;
    &:hover {
      // color: $dark-peach;
      background: transparent;
      background-color: #f0fbfb;
    }
    &:active {
      // color: #ffffff;
    }
  }
}

.navbar-toggler {
  padding: 0.25rem 0;
  border: none;
  color: #000;
  &:focus {
    outline: none!important;
  }
}

.navbar-toggler-icon {
  width: 2rem;
  height: 2rem;
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 30 30' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='#fff' stroke-width='3' stroke-linecap='butt' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E");
  @media only screen and (max-width: 768px) {
    margin-left: 0;
  }
}

// select2 styles
.select2 .selection {
  .select2-selection {
    display: flex;
    align-items: center;
  }
  .select2-selection__arrow {
    height: 20px;
    width: 14px;
    position: absolute;
    top: 0px;
    right: 10px;
    bottom: 0;
    margin: auto;
    background-image: url(../img/arrow.svg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    transition: transform 0.2s ease;
    b {
      display: none;
    }
  }
}

.select2 {
  width: 100px!important;
  .select2-selection__placeholder {
    color: #404852;
  }
  @media only screen and (max-width: 480px) {
    .select2-selection__placeholder {
      font-size: 13px!important;
    }
  }
}

.select2-container--default .select2-selection--single {
  background-color: transparent;
  border: none;
}

.select2-selection__rendered {
  padding-left: 33px!important;
  padding-right: 14px!important;
  background-repeat: no-repeat;
  background-size: 20px 20px;
  background-position: 0 center;
  font-weight: bold;
  &[title=ENG] {
    background-image: url('../img/flags/eng.svg');
  }
  &[title=ES] {
    background-image: url('../img/flags/es.svg');
  }
  &[title=DE] {
    background-image: url('../img/flags/de.svg');
  }
  &[title=FR] {
    background-image: url('../img/flags/fr.svg');
  }
  &[title=IT] {
    background-image: url('../img/flags/it.svg');
  }
}

.select2-container--default .select2-selection--single .select2-selection__rendered {
  color: #fff;
  line-height: 28px;
}

.select2-dropdown {
  border: 1px solid #bec9de;
  border-radius: 0px 0px 4px 4px;
  box-shadow: 1px 4px 10px -2px rgba(0, 0, 0, 0.2);
  .select2-search {
    display: none;
  }
}

.select2-results__option {
  padding-left: 0!important;
}

.select2-container--open .select2-selection__arrow {
  transform: rotate(180deg);
}

.select2-container--default .select2-results__option--highlighted[aria-selected] {
  background-color: #5897fb;
  background-image: linear-gradient(131deg, #99d0f8, #699de1);
  color: white;
}

.select-flag {
  display: inline-block;
  width: 20px;
  height: 20px;
}

// Profile data
.profile {
  display: flex;
  align-items: center;
  &__item {
    display: flex;
    align-items: center;
    margin-right: 2vw;
  }
  .profile__ic {
    display: flex;
    align-items: center;
    img {
      max-width: 36px;
      margin-right: 12px;
    }
  }
  .icon {
    margin-right: 12px;
  }
  &__item.balance {
    color: $dark-peach;
    .icon {
      fill: $dark-peach;
    }
  }
}

// MAIN
.main {
  display: flex;
  justify-content: space-between;
  background-size: cover;
}

.rollers {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  .sidebar {
    width: 100%;
    max-width: 768px;
    height: auto;
    background-color: transparent;
  }
  .toplist__feed {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 1rem;
  }
  .toplist__item {
    flex-basis: 50%;
    background: transparent!important;
    &:nth-child(3),
    &:nth-child(4) {
      background: linear-gradient(to right, #060c22 0%, rgba(11, 21, 52, 1) 95%, rgba(11, 21, 52, 1) 100%)!important;
      ;
    }
    &:before,
    &:after {
      display: none;
    }
  }
}

.scroll-y {
  overflow-y: scroll;
  &::-webkit-scrollbar {
    width: 4px;
  }
  &::-webkit-scrollbar-track {
    background-color: transparent;
  }
  &::-webkit-scrollbar-thumb {
    background-color: rgba(255, 255, 255, 0.1);
    border-radius: 2px;
  }
}